import React, { Component } from 'react';
import './css/App.css';
import Navbar from './Navbar';
import Main from './Main';
import {BrowserRouter as Router } from 'react-router-dom';
class App extends Component {
  render() {
    return (
      <div className="content">
          <Router>
              <div>
                  <Navbar/>
                  <Main/>
              </div>
          </Router>
      </div>
    );
  }
}

export default App;



