import React from 'react'
import { Switch, Route } from 'react-router-dom'
import Start from './Start';
import Library from './Library';
import About from './About';
import './css/Main.css';


// The Main component renders one of the three provided
// Routes (provided that one matches). Both the /roster
// and /schedule routes will match any pathname that starts
// with /roster or /schedule. The / route will only match
// when the pathname is exactly the string "/"
const Main = () => (
    <div className="content-space">
        <Switch>
            <Route exact path='/signature-ui/' component={Start}/>
            <Route exact path='/signature-ui/about' component={About}/>
            <Route exact path='/signature-ui/library' component={Library}>

                <Route path='/signature-ui/library#textbutton' component={Library}/>
                <Route exact path='/signature-ui/library#iconbutton' component={Library}/>
                <Route exact path='/signature-ui/library#textinput' component={Library}/>
                <Route exact path='/signature-ui/library#radio' component={Library}/>
                <Route exact path='/signature-ui/library#checkbox' component={Library}/>
                <Route exact path='/signature-ui/library#dropdown' component={Library}/>
                <Route exact path='/signature-ui/library#combodropdown' component={Library}/>
                <Route exact path='/signature-ui/library#multiselect' component={Library}/>
                <Route exact path='/signature-ui/library#multiselectsearch' component={Library}/>

            </Route>

            {/*<Route exact path='/signature-ui/library#textbutton' component={Library}/>
            <Route exact path='/signature-ui/library#iconbutton' component={Library}/>
            <Route exact path='/signature-ui/library#textinput' component={Library}/>
            <Route exact path='/signature-ui/library#radio' component={Library}/>
            <Route exact path='/signature-ui/library#checkbox' component={Library}/>
            <Route exact path='/signature-ui/library#dropdown' component={Library}/>
            <Route exact path='/signature-ui/library#combodropdown' component={Library}/>
            <Route exact path='/signature-ui/library#multiselect' component={Library}/>
            <Route exact path='/signature-ui/library#multiselectsearch' component={Library}/>*/}



        </Switch>
    </div>
);

export default Main;



