import React, { Component } from 'react';
import './css/Navbar.css';
import './css/App.css';
import {HashLink as Link} from 'react-router-hash-link';
import FaFacebookOfficial from 'react-icons/lib/fa/facebook-official';
import FaMedium from 'react-icons/lib/fa/medium';
import FaInstagram from 'react-icons/lib/fa/instagram';

export class Navbar extends Component {

  render() {
    return (
      <div className="navbar">

        <div className="title">
          <img src="logo_2.png" className="App-logo" alt="logo" />
        </div>

        <ul className="main">
            <li><Link to="/signature-ui/" className="link" activeStyle={{color:'red'}}>Getting Started</Link></li>
            <li><Link to="/signature-ui/about" className="link" activeStyle={{color:'red'}}>About Signature UI</Link></li>
          <li><Link to="/signature-ui/library" className="link" activeStyle={{color:'red'}}>Signature UI</Link>

              <ul className="submenu">

                  <li><Link to="/signature-ui/library#textbutton" className="link">Text Button</Link></li>
                  <li><Link to="/signature-ui/library#iconbutton" className="link">Icon Button</Link></li>
                  <li><Link to="/signature-ui/library#textinput" className="link">Text Input</Link></li>
                  <li><Link to="/signature-ui/library#radio" className="link">Radio</Link></li>
                  <li><Link to="/signature-ui/library#checkbox" className="link">Checkbox</Link></li>
                  <li><Link to="/signature-ui/library#dropdown" className="link">Dropdown</Link></li>
                  <li><Link to="/signature-ui/library#combodropdown" className="link">Combodropdown</Link></li>
                  <li><Link to="/signature-ui/library#multiselect" className="link">Multisearch</Link></li>
                  <li><Link to="/signature-ui/library#multiselectsearch" className="link">Multiselect Search</Link></li>

                  {/*
                  <li><a href="/signature-ui/library#textbutton">Text Button</a></li>
                  <li><a href="/signature-ui/library#iconbutton">Icon Button</a></li>
                  <li><a href="/signature-ui/library#textinput">Text Input</a></li>
                  <li><a href="/signature-ui/library#radio">Radio</a></li>
                  <li><a href="/signature-ui/library#checkbox">Checkbox</a></li>
                  <li><a href="/signature-ui/library#dropdown">Dropdown</a></li>
                  <li><a href="/signature-ui/library#combodropdown">Combo Dropdown</a></li>
                  <li><a href="/signature-ui/library#multiselect">Mulitselect</a></li>
                  <li><a href="/signature-ui/library#multiselectsearch">Multiselect Search</a></li>*/}

              </ul>

          </li>
            <li>
                <ul className="submenu2">
                    <li>Follow us on
                        <a href="https://www.facebook.com/GrondaGastro/" target="blank" className="social-icon"><FaFacebookOfficial/></a>
                        <a href="https://medium.com/gronda" target="blank" className="social-icon"><FaMedium/></a>
                        <a href="https://www.instagram.com/gronda.eu" target="blank" className="social-icon"><FaInstagram/></a>
                    </li>

                    <li>&copy; Gronda 2018</li>
                </ul>
            </li>
        </ul>





      </div>
    );
  }
}

export default Navbar;



